import React, { useState } from "react";
import Honors from "../../components/Honors";
import IBChoice from "../../components/IBChoice";
import SWOT from "../../components/SWOT";
import Universities from "../../components/Universities";

const Ninth = ({ initialData, setProfile }) => {
  const [data, setData] = useState(initialData);
  const getData = (type, value) => {
    const newData = { ...data, [type]: value };
    setData(newData);
    setProfile("grade10", newData);
  };
  return (
    <div className="space-y-10">
      <p className="text-xl font-bold">
         VI.   (3rd year (9 grade).
      </p>
      <p>1.   ? </p>
      <SWOT setParentData={getData} dataName="profession1" />
      <SWOT setParentData={getData} dataName="profession2" />
      <p>2.University of my dream </p>
      <Universities setParentData={getData} />
      <div>
        <p>3.  .</p>
        <p>
           .   Motivational Letter, Personal Statement
          Essay       
           .
        </p>
      </div>
      <Honors setParentData={getData} />
      <p>
        4.       
        .
      </p>
      <IBChoice setParentData={getData} />
    </div>
  );
};

export default Ninth;
